<template>
    <div class="carconstrol">
        <transition name="move">
            <span class="decrease icon-remove_circle_outline" v-show="count !== 0" @click="remove"></span>
        </transition>
       
        <span class="count" v-show="count !== 0">{{count}}</span>
        <div class="add icon-add_circle" @click="add" >
                <div class="parabola"  ref="parabola">
                    <transition name='add'>
                        <div class="addAnimate"></div>
                    </transition>
                 </div>
        </div>

        <!-- 抛物线动画 -->
        
       
    </div>
</template>


<script>
    import Vue from 'vue';
export default {
    props:{
        foods:{
            type : Object
        }
    },
    computed:{
        count(){
            let res = this.foods.count
            return res
        }
    },
    
    methods:{
        remove(price,which,event){
            if(this.foods.count === 0){
                return
            }
            this.foods.count --;
        },
        add(event){
            
            let parabola = this.$refs.parabola;
            let x = event.clientX;
            let y = event.clientY;
            console.log(parabola,x,y);
            // this.addAnimate(parabola,x,y);
            if(!this.foods.count){
                Vue.set(this.foods,'count');
                this.foods.count = 1;
            }else {
                this.foods.count ++;
            }
            
        },
        addAnimate(ele,x,y){
            clearInterval(ele.timer)
            ele.style.opacity = 1;
            let iSpeedX = 0,iSpeedY = 0, _top = 0
            ele.timer = setInterval(function(){
                
                ele.style.top = _top - iSpeedX + 'px';
                ele.style.left = -iSpeedX + 'px';
                _top = parseInt(ele.style.top);
                iSpeedY = ( _top - y -200) / 7
                // if(parseInt(ele.style.top) <= 218){
                //     iSpeedY
                // }
                console.log(_top)
                iSpeedX += x * 0.1
            },300)
        }
    }
}
</script>
<style lang="stylus">
    .carconstrol
        display flex
        align-items center
        .decrease,.add
            padding 4px
            font-size 24px
            color rgb(0,160,250)
            transition: all 0.4s linear
            transform: rotate(0)
            &.move-enter-active, &.move-leave-active
                transition: all 0.4s linear
            &.move-enter, &.move-leave-active
                opacity: 0
                transform: translate3d(24px, 0, 0)
                .decrease
                    transform: rotate(180deg)
        .count
            width 16px
            font-size 10px
            color rgb(147,153,159)
            line-height 25px
            height 25px
            text-align center
            vertical-align top
        .parabola
            position absolute
            top -20px
            left 2px
            width 18px
            height 18px
            border-radius 50%
            opacity 0
            background-color rgb(0,160,250)
            z-index 900
            
</style>



